import React from 'react'
import avatar from "../../assets/perfil.jpg"
import PhoneAndroid from '@material-ui/icons/PhoneAndroid'
import Email from '@material-ui/icons/Email'
import {GitHub} from '@material-ui/icons'
import School from '@material-ui/icons/School'
import DateRange from '@material-ui/icons/DateRange'
import Language from '@material-ui/icons/Language'
import study from '../../assets/study'

const Me = () => {
  const person = {
      name: 'Ernesto Jos√© Barracchia',
      phone: '+54-9-11-49746437',
      email: 'ebarracchia@gmail.com',
      github: 'https://github.com/ebarracchia',
      quote: 'Fan√°tico de las nuevas tecnolog√≠as y nuevos desaf√≠os que surgen constantemente.'
  }

  return (
    <div className="avatar">
      <div className="avatar-photo">
        <img alt="perfil" src={avatar} />
      </div>
      <div className="avatar-information">
        <div className="avatar-information-name">{person.name}</div>
        <div className="avatar-information-phone"><PhoneAndroid /> {person.phone}</div>
        <div className="avatar-information-email"><span className='avatar-icon'><Email /></span> {person.email}</div>
        <div className="avatar-information-github"><span className='avatar-icon'><GitHub /></span> {person.github}</div>
        <div className="avatar-information-quote">{person.quote}</div>
      </div>
      <div className="avatar-study">
        <div className="avatar-study-title">{study.title}</div>
        <div className="avatar-study-university"><School /> {study.university}</div>
        <div className="avatar-study-more"><DateRange /> {study.more}</div>
        <div className="avatar-study-idioms"><Language /> {study.idioms}</div>
      </div>
    </div>
  );
};

export default Me