import React, { useState } from 'react'
import { Chrono } from "react-chrono";
import {data} from "../../assets/work";
import BorderLeft from '@material-ui/icons/BorderLeft'
import BorderVertical from '@material-ui/icons/BorderVertical'
import Tooltip from '@material-ui/core/Tooltip';

const Cv = () => {
  const [verticalAlternate, setVerticalAlternate] = useState(false)

  let button;
  if (verticalAlternate) {
    button = <Tooltip title="Set Vertical">
                <BorderLeft className="button-layout" 
                  onClick={() => setVerticalAlternate(!verticalAlternate)} />
              </Tooltip>
  } else {
    button = <Tooltip title="Set Vertical Alternate">
                <BorderVertical className="button-layout" 
                  onClick={() => setVerticalAlternate(!verticalAlternate)} />
              </Tooltip>
  }

  return (
    <div>
      <div className="toolbar">
        {button}
      </div>
      <Chrono 
        items={data} 
        theme={{primary: "blue", secondary: "lightgray" }}
        mode={verticalAlternate ? "VERTICAL_ALTERNATING" : "VERTICAL" }>
        {data.map((elem, i) => <ul key={i}>
            {elem.items.map((item, i) => <li key={i}>{'üè∑Ô∏è - ' + item}</li> )}
          </ul>
        )}
      </Chrono>
    </div>
  );
};

export default Cv