import React from 'react'
import Favoritos from '../other/Favoritos'
import imgProductOwner from "../../assets/product-owner.png"
import imgCursoReact from "../../assets/curso-react.png"
import imgCursoAwsArchitecting from "../../assets/curso-aws-architecting.png"

function Otros() {
    return (
        <div className="otros-container">
            <Favoritos />
            <div className="otros-imagen">
                <img alt="" src={imgProductOwner} />
                <br />
                <img alt="" src={imgCursoReact} />
                <br />
                <img alt="" src={imgCursoAwsArchitecting} />
                <br />
            </div>
        </div>
    )
}

export default Otros
